                        <template v-for="(val, key) in o">
                            <span class="u-desc" :key="key" v-if="cansee(o, key)">
                                <el-tooltip effect="light" :content="key | showKeyLabel" placement="top">
                                    <em>{{ key }}</em>
                                </el-tooltip>
