                </div>
            </li>
        </ul>
        <el-alert v-if="!list.length && done" title="没有找到相关条目" type="info" show-icon></el-alert>
    </div>
</template>

<script>
import npcmap from "@/assets/data/npc.json";
export default {
    name: "npc",
    props: ["data", "vip", "status"],
    data: function() {
        return {
            list: this.data || [],
            npcmap,
        };
    },
    computed: {
        hasRight: function() {
            return this.vip;
        },
        done: function() {
            return this.status;
        },
    },
    watch: {
        data: {
            deep: true,
            handler: function(val) {
                this.list = val;
            },
        },
    },
    filters: {
        iconURL: function(id) {
            return __iconPath + "icon/" + id + ".png";
        },
        filterRaw: function(str) {
            return str && str.replace(/\\n/g, "\n");
        },
    },
    methods: {
        onCopy: function(val) {
            this.$notify({
                title: "复制成功",
                message: "复制内容 : " + val.text,
                type: "success",
            });
        },
        onError: function() {
            this.$notify.error({
                title: "复制失败",
                message: "请手动复制",
            });
        },
        toggleProps: function(o) {
            o.isopen = !o.isopen;
        },
        cansee: function(o, key) {
            // 本地虚拟字段
            if (key == "isopen" || key == "IdKey") return false;

            // 值为null实
            if (o[key] === null) return false;

            // 不包含基础类型
            if (npcmap?.[key]?.["basic"]) return false;

            return true;
        },
    },
    filters: {
        showKeyLabel: function(key) {
            return npcmap[key]?.["desc"] || key;
        },
    },
};
</script>
