</template>

<script>
import Nav from "@/components/Nav.vue";
import { loadResource, loadStat } from "../../service/database";
import { __iconPath, __ossRoot } from "@jx3box/jx3box-common/data/jx3box.json";
import User from "@jx3box/jx3box-common/js/user";
import schools from "@jx3box/jx3box-data/data/xf/forceid.json";
import data_buff from "./data_buff.vue";
import data_skill from "./data_skill.vue";
import data_npc from "./data_npc.vue";
import data_doodad from "./data_doodad.vue";
import { __imgPath } from "@jx3box/jx3box-common/data/jx3box.json";
export default {
    name: "Database",
    props: [],
    data: function() {
        return {
            type: "buff",
            query: "",
            npc_map: "",
            npc_level: "",
            doodad_level: "",
            strict: false,
            level: "",
            school: "",

            skill: [],
            advskill: [],
            buff: [],
            advbuff: [],
            npc: [],
            item: [],
            doodad: [],

            stat: {
                skill: 0,
                buff: 0,
                item: 0,
                npc: 0,
                doodad: 0,
            },
            done: false,
            loading: false,

            isSuperAuthor: User.isSuperAuthor(),
            schools,

            per: 10,
            page: 1,
            total: 1,
            pages: 1,

            hasRight: false,

            client: "std",
        };
    },
    computed: {
        isBlank: function() {
            return !this.query && !this[this.type]["length"];
        },
        hasNextPage: function() {
            return this.total > 1 && this.page < this.pages;
        },
        multipage: function() {
            return this.done && this.pages > 1;
        },
    },
    methods: {
        getIcon(key) {
            return __imgPath + "image/box/" + key + ".svg";
        },
        getData: function(page = 1, append = false) {
            if (!this.query) return;

            this.loading = true;
            this.done = false;
            let query = this.query;

            let params = {
                strict: ~~this.strict,
                per: this.per,
                page: page,
                client: this.client,
            };
            if (this.type == "npc") {
                if (this.npc_map) params.map = this.npc_map;
                if (this.npc_level) params.level = this.npc_level;
            }
            if (this.type == "skill" || this.type == "buff") {
                if (this.level) params.level = this.level;
            }
            if (this.type == "skill") {
                if (this.school || this.school == 0) params.school = this.school;
            }
            if (this.type == "doodad") {
                if (this.doodad_level) params.level = this.doodad_level;
            }

            let mode = isNaN(query) ? "name" : "id";
            loadResource(this.type, mode, query, params)
                .then((data) => {
                    if (append) {
                        this[this.type] = this[this.type].concat(this.transformData(data.list));
                    } else {
                        window.scroll(0, 0);
                        this[this.type] = this.transformData(data.list);
                    }
                    this.pages = data.pages;
                    this.total = data.total;
                })
                .finally(() => {
                    this.done = true;
                    this.loading = false;
                });
        },
        search: function() {
            this.page = 1;
            this.getData();
        },
        toggleProps: function(o) {
            o.isopen = !o.isopen;
        },
        transformData: function(data) {
            data.forEach((item) => {
                item.isopen = false;
            });
            return data;
        },
        appendPage: function() {
            this.getData(++this.page, true);
        },
        changePage: function(i) {
            this.getData(i);
        },
        changeType: function() {
            this.page = 1;
            this.getData();
        },
    },
    filters: {},
    created: function() {
        this.client = this.$store.state.client;

        loadStat(this.client).then((data) => {
            this.stat = data;
        });

        User.isLogin() &&
            User.isVIP().then((data) => {
                this.hasRight = data;
            });
    },
    mounted: function() {
        let params = new URLSearchParams(location.search);
        this.type = params.get("type") || "buff";
        this.query = params.get("query") || "";
        this.level = params.get("level") || "";
        this.search();
    },
    components: {
        Nav,
        "data-buff": data_buff,
        "data-skill": data_skill,
        "data-npc": data_npc,
        "data-doodad": data_doodad,
    },
};
</script>

<style lang="less">
@import "../../assets/css/database.less";
</style>
